<template>
	<Tabs value="credit" class="tab">
		<Tab-pane label="贷后详情" name="credit" class="tab-item">
			<div class="order-cell">
				<div class="cell-title">贷款信息表</div>
				<div class="cell-body">
					<div class="cell-item">
						<span class="order-item-title">贷款编号：</span><span>{{loanInfo.loanId | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">期数：</span><span>{{loanInfo.periods | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">贷款客户：</span><span>{{loanInfo.name | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">身份证号：</span><span>{{loanInfo.idNumber | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">业务城市：</span><span>{{loanInfo.city | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">贷款产品：</span><span>{{loanInfo.eProduct | empty}}</span>
					</div>
					<div class="cell-item" v-if="loanInfo.repaymentSchedule">
						<span class="order-item-title">贷款总额：</span><span>{{loanInfo.repaymentSchedule.loanAmount | fmoney | empty}}</span>
					</div>
					<div class="cell-item" v-if="loanInfo.repaymentSchedule">
						<span class="order-item-title">放款日期：</span><span>{{loanInfo.repaymentSchedule.loanDate | empty | fDate }}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">平台及利率：</span><span>新网-{{loanInfo.repayWay | empty}}-{{loanInfo.rate | empty}}%</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">银行卡号：</span><span>{{loanInfo.cCardNumber | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">开户行：</span><span>{{loanInfo.cCardBank | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">持卡人：</span><span>{{loanInfo.cCardName | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">放款方式：</span><span>{{loanInfo.repayWay | empty}}</span>
					</div>
					<div class="cell-item">
						<span class="order-item-title">备注：</span><span>{{loanInfo.remark | empty}}</span>
					</div>
				</div>
			</div>
			<div class="table-cell">
				<div class="cell-title">还款计划表</div>
				<div class="table-body">
					<Table size="large" highlight-row :columns="header" :data="cList[page-1]"></Table>
					<div class="page">
						<Page placement='top' @on-change="pageChange" @on-page-size-change="sizeChange"
							  :total="totalPage"
							  show-elevator
							  show-sizer
							  show-total :page-size="pageSize" :page-size-opts="[10,20,30,40]">
						</Page>
					</div>
				</div>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-undo-outline" @click="$router.go(-1)">返回</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				header: [
					{
						title: '期数',
						key: 'index',
						align: 'center'
					},
					{
						title: '还款日期',
						key: 'repaydate',
						align: 'center'
					},
					{
						title: '本金',
						key: 'capital',
						align: 'center',
						render: (h, params) => {
							return h('span', this.fmoney(params.row.capital))
						}
					},
					{
						title: '利息',
						key: 'interest',
						align: 'center',
						render: (h, params) => {
							return h('span', this.fmoney(params.row.interest))
						}
					},
					{
						title: '违约金',
						key: 'lateFee',
						align: 'center',
						render: (h, params) => {
							return h('span', this.fmoney(params.row.lateFee))
						}
					},
					{
						title: '本期应还',
						key: 'mastPay',
						align: 'center',
						render: (h, params) => {
							let mastPay=params.row.capital+params.row.interest;
							return h('span', this.fmoney(mastPay))
						}
					},
					{
						title: '本期已还',
						key: 'total',
						align: 'center',
						render: (h, params) => {
							return h('span', this.fmoney(params.row.total))
						}
					}
				],
				list: [],
				cList: [],
				loanInfo:{},
				totalPage: 10,
				pageSize: 10,
				page: 1,
			}
		},
		created: function () {
			this.loadList();
		},
		methods: {
			loadList: function () {
				var _this = this;
				let loanId = _this.$route.params.loanId;
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('AfterLoan/loanInformation', {
					loanId: loanId
				})
					.then(function (response) {
						let res = response.data;
						let list=res.data.repaymentSchedule;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == 1) {
							if(res.data){
								_this.loanInfo=res.data;
								if(list){//表格
									_this.totalPage = _.size(list.plants);//设置页码
									_this.list = list.plants;//原始数据
									_this.cList = _.chunk(list.plants, _this.pageSize);//表格数据
								}
							}
						} else {
							_this.$Notice.error({
								title: res.msg
							})
							_this.$router.go(-1);
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Notice.error({
							title: error
						})
						_this.$router.go(-1);
					})
			},
			sizeChange: function (size) {
				let _this = this;
				_this.pageSize = size;
				_this.cList = _.chunk(_this.list, size);
			},
			pageChange: function (page) {
				let _this = this;
				if (_this.page != page) {
					_this.page = page;
				}
			},
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.order-title {
		text-align: right;
		color: #343434;
		font-size: 14px;
	}

	.order-cell {
		padding-bottom: 5px;
		border-bottom: 1px solid #e5e5e5;
		padding-top: 30px;
	}

	.table-cell {
		padding: 30px 0;
	}

	.cell-title {
		height: 14px;
		border-left: 3px solid #ff8226;
		color: #ff8226;
		font-size: 14px;
		line-height: 14px;
		padding-left: 10px;
		margin-bottom: 30px;
	}

	.cell-body {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: flex-start;
		align-items: center;
	}

	.cell-item {
		min-width: 300px;
		padding-bottom: 15px;
		padding-right: 20px;
		color: #333333;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		font-size: 14px;
	}

	.order-item-title {
		width: 100px;
		height: 22px;
		line-height: 22px;
		color: #7f7f7f;
		overflow: hidden;
	}
</style>



